@import "./nullstyle";

body {
  background-color: #1a1a2e;
  /* темно-синій фон */
  padding: 40px;
  font-family: 'Inter', sans-serif;
  color: #e0e0e0;
}

.wrapper {
  background-color: #16213e;
  /* глибокий синій */
  max-width: 1440px;
  margin: 0 auto;
  border-radius: 14px;

  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.content {
  padding: 30px 50px;
  flex: 1 1 auto;
}

.button {
  padding: 10px 18px;
  border-radius: 8px;
  background-color: #ff6b6b;
  /* яскравий червоний */
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  border: 2px solid #ff4d4d;
  box-shadow: 0 4px 8px rgba(255, 107, 107, 0.5);
  cursor: pointer;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;

  &:hover {
    background-color: #ff4d4d;
    border-color: #ff1a1a;
    box-shadow: 0 6px 12px rgba(255, 77, 77, 0.7);
  }

  &:active {
    background-color: #cc3838;
    border-color: #991f1f;
    box-shadow: 0 2px 6px rgba(204, 56, 56, 0.8);
  }
}

.bigbutton {
  font-size: 28px;
  padding: 14px 32px;
}

.buybutton {
  background-color: #28a745;
  color: white;
  font-weight: 700;
  font-size: 28px;
  padding: 16px 32px;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  margin-top: 20px;
  box-shadow: 0 6px 12px rgba(40, 167, 69, 0.6);
  transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.2s ease;

  &:hover {
    background-color: #218838;
    box-shadow: 0 8px 20px rgba(33, 136, 56, 0.8);
    transform: translateY(-3px);
  }

  &:active {
    background-color: #1e7e34;
    box-shadow: 0 4px 8px rgba(30, 126, 52, 0.7);
    transform: translateY(0);
  }
}


.page {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding-bottom: 50px;

  &__button {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}

.pageflex {
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
  justify-content: space-around;
}

.clothes-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
  padding: 24px;
}

.clothes {
  background-color: #0f3460;
  border-radius: 20px;
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  position: relative;
  box-shadow: 0 8px 20px rgba(15, 52, 96, 0.7);
  transition: transform 0.25s ease, box-shadow 0.25s ease;

  max-width: 280px;
  width: 100%;

  &:hover {
    transform: scale(1.03);
    box-shadow: 0 12px 30px rgba(83, 53, 74, 0.8);
  }

  &__img {
    width: 100%;
    max-height: 350px;
    border-radius: 12px;
    object-fit: cover;
  }

  &__name {
    color: #f0f0f0;
    font-size: 26px;
    font-weight: 700;
    text-align: center;
  }

  &__price {
    color: #a0a0a0;
    font-size: 16px;
    text-align: center;
    margin-top: -8px;
    margin-bottom: 4px;
  }

  &__unit-price {
    color: #ffb142;
    font-size: 22px;
    font-weight: 600;
    text-align: center;
  }

  &__plus-minus {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
  }

  &__count {
    color: #f0f0f0;
    font-size: 20px;
    font-weight: 600;
    min-width: 36px;
    text-align: center;
  }

  &__count-cart {
    margin: 0 10px;
  }

  &__remove-all {
    position: absolute;
    top: 16px;
    right: 16px;
    background: transparent;
    border: none;
    color: #ff6b6b;
    font-size: 24px;
    cursor: pointer;
    transition: transform 0.3s ease, color 0.3s ease;

    &:hover {
      transform: scale(1.4);
      color: #ff3b3b;
    }
  }
}

.empty {
  width: 100%;
  color: #cccccc;
  font-size: 54px;
  text-align: center;
  padding: 60px;
}

.footer {
  padding: 40px 0;
  text-align: center;
  font-size: 42px;
  background: #1f4068;
  color: #d0d0d0;
}

.header {
  padding: 40px 50px;
  background: #1f4068;
  display: flex;
  justify-content: space-between;

  &__logo {
    font-size: 42px;
    font-weight: 900;
    color: #f0f0f0;
  }
}

@media (max-width: 1024px) {
  .clothes-list {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    padding: 20px;
  }
}

@media (max-width: 600px) {
  .clothes-list {
    grid-template-columns: 1fr;
    padding: 16px;
  }
}